import React from "react";
import Card from "react-bootstrap/Card";

const AllBills = () => {
  return (
    <>
      <Card>
        <Card.Header>
          <strong>All Bills</strong>
        </Card.Header>
        <Card.Body></Card.Body>
        <Card.Footer></Card.Footer>
      </Card>
    </>
  );
};

export default AllBills;
